"use client"

import { useState } from "react"
import { useStore } from "@/contexts/store-context"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Plus, Pencil, Trash2, X } from "lucide-react"

export default function AdminCategoriesPage() {
    const { categories, addCategory, updateCategory, deleteCategory } = useStore()
    const [isAddingRequest, setIsAddingRequest] = useState(false)
    const [editingId, setEditingId] = useState<string | null>(null)
    const [formData, setFormData] = useState({ name: "", description: "" })
    const [error, setError] = useState<string | null>(null)

    const resetForm = () => {
        setFormData({ name: "", description: "" })
        setIsAddingRequest(false)
        setEditingId(null)
        setError(null)
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        setError(null)
        try {
            if (editingId) {
                await updateCategory(editingId, formData)
            } else {
                await addCategory(formData as { name: string, description: string })
            }
            resetForm()
        } catch (err: any) {
            setError(err.message)
        }
    }

    const handleEdit = (category: any) => {
        setFormData({ name: category.name, description: category.description || "" })
        setEditingId(category.id)
        setIsAddingRequest(true)
        setError(null)
    }

    const handleDelete = async (id: string) => {
        if (confirm("Are you sure?")) {
            try {
                await deleteCategory(id)
            } catch (err: any) {
                alert(err.message)
            }
        }
    }

    return (
        <div>
            <div className="mb-8 flex items-center justify-between">
                <div>
                    <h1 className="mb-2 text-3xl font-bold">Category Management</h1>
                    <p className="text-muted-foreground">Manage your product categories.</p>
                </div>
                {!isAddingRequest && (
                    <Button onClick={() => setIsAddingRequest(true)}>
                        <Plus className="mr-2 h-4 w-4" /> Add Category
                    </Button>
                )}
            </div>

            {isAddingRequest && (
                <Card className="mb-6">
                    <CardHeader>
                        <div className="flex justify-between items-center">
                            <CardTitle>{editingId ? "Edit Category" : "New Category"}</CardTitle>
                            <Button variant="ghost" size="icon" onClick={resetForm}><X className="w-4 h-4" /></Button>
                        </div>
                    </CardHeader>
                    <CardContent>
                        {error && <div className="mb-4 text-sm text-destructive font-medium">{error}</div>}
                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div className="space-y-1">
                                <Label htmlFor="catName">Name</Label>
                                <Input
                                    id="catName"
                                    required
                                    value={formData.name}
                                    onChange={e => setFormData(p => ({ ...p, name: e.target.value }))}
                                />
                            </div>
                            <div className="space-y-1">
                                <Label htmlFor="catDesc">Description</Label>
                                <Textarea
                                    id="catDesc"
                                    value={formData.description}
                                    onChange={e => setFormData(p => ({ ...p, description: e.target.value }))}
                                />
                            </div>
                            <div className="flex justify-end gap-2">
                                <Button type="button" variant="outline" onClick={resetForm}>Cancel</Button>
                                <Button type="submit">Save</Button>
                            </div>
                        </form>
                    </CardContent>
                </Card>
            )}

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {categories.map((category) => (
                    <Card key={category.id}>
                        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                            <CardTitle className="text-sm font-medium">{category.name}</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="text-xs text-muted-foreground mb-4 h-10 overflow-hidden text-ellipsis">
                                {category.description || "No description"}
                            </div>
                            <div className="flex gap-2">
                                <Button size="sm" variant="outline" className="w-full" onClick={() => handleEdit(category)}>
                                    <Pencil className="w-3 h-3 mr-1" /> Edit
                                </Button>
                                <Button size="sm" variant="destructive" className="w-full" onClick={() => handleDelete(category.id)}>
                                    <Trash2 className="w-3 h-3 mr-1" /> Delete
                                </Button>
                            </div>
                        </CardContent>
                    </Card>
                ))}
            </div>
        </div>
    )
}
